import React from "react";
import { getBoardArray } from "../getArrayFunctions";
import { Cell } from "./Cell";

interface PlayerInterface {
  name: string;
  move: Array<number>;
  ships: Array<number>;
  destroyedShips: Array<number>;
}

interface SeaInterface {
  seaSize: number;
  currentPlayer: string | null;
  player: PlayerInterface;
  oponent: PlayerInterface;
  onTarget: (id: number) => void;
}

interface StringStringInterface {
  [key: string]: string;
}

const playerColor: StringStringInterface = {
  player1: "green-500",
  player2: "yellow-500",
};

const playerShip: StringStringInterface = {
  player1: "rounded-l-none rounded-r-full",
  player2: "rounded-r-none rounded-l-full",
};

export function Sea({
  seaSize,
  currentPlayer,
  player,
  oponent,
  onTarget,
}: SeaInterface) {
  return (
    <div className={`w-1/2 p-2 bg-white text-${playerColor[player.name]}`}>
      <p>
        {player.name} have a{" "}
        <span className="font-bold">{player.ships.length}</span>{" "}
        {player.ships.length === 1 ? "ship" : "ships"}
      </p>
      <div className={`flex flex-wrap bg-blue-800`}>
        {getBoardArray(seaSize).map((id) => {
          return (
            <div
              key={id}
              className={`w-1/${seaSize} ${playerShip[player.name]} ${
                player.destroyedShips.includes(id)
                  ? `bg-${playerColor[player.name]}`
                  : null
              }`}
            >
              <Cell
                id={id}
                disabled={
                  currentPlayer === player.name || oponent.move.includes(id)
                }
                onTarget={onTarget}
                xChoice={oponent.move.includes(id)}
                destroyed={player.destroyedShips.includes(id)}
              />
            </div>
          );
        })}
      </div>
    </div>
  );
}
